<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <el-row :gutter="20">
          <el-col :span="5">
            <el-form-item :label="$t('know.docName')" prop="docName">
              <el-input
                v-model="qryParam.docName"
                :placeholder="inputPh('know.docName')"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="19">
            <el-form-item label-width="0">
              <div class="search-button-group">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="qryCmpParamPage(false)"
                  >{{ $t('common.query') }}</el-button
                >
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
              </div>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <ct-table
        ref="table"
        v-loading="loading"
        :data="tableData"
        :border="true"
        :pagination="pagination"
        :total="total"
        @change="tableChange"
        @selection-change="handleSelectionChange"
      >
        <el-table-column prop="docName" :label="$t('know.docName')" min-width="110">
        </el-table-column>
        <el-table-column
          prop="docDesc"
          :label="$t('know.docDesc')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="docTypeName"
          :label="$t('know.docTypeName')"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <!-- <el-table-column
          prop="createStaffName"
          :label="$t('know.createStaffName')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column> -->
        <el-table-column
          prop="createTime"
          :label="$t('know.createTime')"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column :label="$t('common.action')" fixed="right" min-width="110">
          <template slot-scope="scope">
            <div class="action-button-group">
              <el-button v-compPriv="50" type="text" @click="modify(scope.row)">{{
                $t('common.download')
              }}</el-button>
            </div>
          </template>
        </el-table-column>
      </ct-table>
    </div>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
export default {
  mixins: [langMixin],
  data() {
    return {
      powerSearch: false,
      loading: false,
      tableData: [],
      typeOptions: [],
      qryParam: {
        name: '',
      },
      pagination: {
        pageIndex: 1,
        pageSize: 10,
      },
      total: 0,
      saleList: [],
      modifyForm: {},
    }
  },
  mounted() {
    this.getBatchTaskAllBusiType()
    this.qryCmpParamPage(false)
  },
  methods: {
    // 获取业务类型
    getBatchTaskAllBusiType() {
      this.$api.userMag.getBatchTaskAllBusiType().then((res) => {
        this.typeOptions = res
      })
    },
    // 查询业务配置
    async qryCmpParamPage(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.pagination.pageIndex = 1
        this.pagination.pageSize = 10
      }
      const param = {
        ...this.qryParam,
        ...this.pagination,
      }
      this.loading = true
      const res = await fetch('/doc/querySysDocumentPage', param, {
        method: 'post',
      }).finally(() => {
        this.loading = false
      })
      this.tableData = res.list || []
      this.total = res.total
    },
    tableChange(pagination) {
      this.pagination = pagination
      this.qryCmpParamPage(true)
    },
    addParam() {
      this.modifyParamDialogVisible = true
      this.modifyTitle = '新建'
    },
    modify(rows) {
      const fileName = rows.fileName
      const url = rows.docUrl
      const link = document.createElement('a')
      document.body.appendChild(link)
      link.style.display = 'none'
      link.href = `${
        process.env.VUE_APP_REQUEST_BASE_API
      }/batch/downloadFile?filePath=${encodeURIComponent(
        url
      )}&fileName=${encodeURIComponent(fileName)}`
      link.click()
      document.body.removeChild(link)
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    selectCust(data) {
      this.selectCustomerVisible = false
      this.qryParam.custId = data.custId
      this.qryParam.custName = data.custName
    },

    // 修改
    modifyParam(value) {
      if (value) {
        this.modifyForm = null
        this.qryCmpParamPage(false)
      }
    },
    clickDelete() {},
    // 重置
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.qryCmpParamPage(false)
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
</style>
